'use client';

import { motion } from 'framer-motion';
import { Trophy, Rocket, Target, ChevronRight } from 'lucide-react';
import Button from '@/components/ui/Button';
import styles from './LevelUpSection.module.css';

const LevelUpSection = () => {
    const features = [
        {
            icon: Trophy,
            title: 'Track Your Progress',
            description: 'See all your applications, wins, and achievements in one place.',
        },
        {
            icon: Rocket,
            title: 'Get Personalized Picks',
            description: 'AI-powered recommendations based on your interests and goals.',
        },
        {
            icon: Target,
            title: 'Never Miss Deadlines',
            description: 'Smart reminders so you never miss an important opportunity.',
        },
    ];

    return (
        <section className={styles.section}>
            <div className={styles.container}>
                <motion.div
                    className={styles.header}
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    viewport={{ once: true }}
                >
                    <h2 className={styles.title}>
                        Level Up Your <span className={styles.highlight}>Journey</span>
                    </h2>
                    <p className={styles.subtitle}>
                        Join a community of ambitious students building their future, one opportunity at a time.
                    </p>
                </motion.div>

                <motion.div
                    className={styles.features}
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    viewport={{ once: true }}
                    transition={{ delay: 0.1 }}
                >
                    {features.map((feature, index) => (
                        <motion.div
                            key={feature.title}
                            className={styles.feature}
                            initial={{ opacity: 0, y: 20 }}
                            whileInView={{ opacity: 1, y: 0 }}
                            viewport={{ once: true }}
                            transition={{ delay: 0.1 + index * 0.1 }}
                        >
                            <div className={styles.featureIcon}>
                                <feature.icon size={24} />
                            </div>
                            <h3 className={styles.featureTitle}>{feature.title}</h3>
                            <p className={styles.featureDescription}>{feature.description}</p>
                        </motion.div>
                    ))}
                </motion.div>

                <motion.div
                    className={styles.leaderboard}
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    viewport={{ once: true }}
                    transition={{ delay: 0.3 }}
                >
                    <div className={styles.leaderboardHeader}>
                        <div className={styles.leaderboardIcon}>üèÜ</div>
                        <div>
                            <h3 className={styles.leaderboardTitle}>Weekly Leaderboard</h3>
                            <p className={styles.leaderboardSubtitle}>Top performers this week</p>
                        </div>
                    </div>
                    <Button variant="outline" size="sm" rightIcon={<ChevronRight size={16} />}>
                        See Rankings
                    </Button>
                </motion.div>
            </div>
        </section>
    );
};

export default LevelUpSection;
