'use client';

import { useRef } from 'react';
import { motion } from 'framer-motion';
import { ChevronLeft, ChevronRight, Flame } from 'lucide-react';
import styles from './TrendingSlider.module.css';

interface TrendingItem {
    id: string;
    title: string;
    subtitle: string;
    color: string;
    emoji: string;
    isHot?: boolean;
}

const trendingItems: TrendingItem[] = [
    {
        id: '1',
        title: 'Google Summer...',
        subtitle: 'Apply Now',
        color: '#ef4444',
        emoji: 'üî¥',
        isHot: true,
    },
    {
        id: '2',
        title: 'Tata Scholarship',
        subtitle: 'Deadline Soon',
        color: '#3b82f6',
        emoji: 'üîµ',
    },
    {
        id: '3',
        title: 'NTSE Olympiad',
        subtitle: 'Registration Open',
        color: '#22c55e',
        emoji: 'üü¢',
    },
    {
        id: '4',
        title: 'Hack the...',
        subtitle: 'Team Registration',
        color: '#a855f7',
        emoji: 'üü£',
    },
    {
        id: '5',
        title: 'Amazon Olympiad',
        subtitle: 'New',
        color: '#f59e0b',
        emoji: 'üü†',
    },
    {
        id: '6',
        title: 'KVPY Fellowship',
        subtitle: 'Apply by Dec',
        color: '#06b6d4',
        emoji: 'üî∑',
    },
];

interface TrendingSliderProps {
    onItemClick?: (id: string) => void;
}

const TrendingSlider = ({ onItemClick }: TrendingSliderProps) => {
    const sliderRef = useRef<HTMLDivElement>(null);

    const scroll = (direction: 'left' | 'right') => {
        if (sliderRef.current) {
            const scrollAmount = 200;
            sliderRef.current.scrollBy({
                left: direction === 'left' ? -scrollAmount : scrollAmount,
                behavior: 'smooth',
            });
        }
    };

    return (
        <section className={styles.section}>
            <div className={styles.header}>
                <div className={styles.titleWrapper}>
                    <Flame size={20} className={styles.fireIcon} />
                    <h3 className={styles.title}>Trending Now</h3>
                </div>
                <div className={styles.controls}>
                    <button className={styles.navBtn} onClick={() => scroll('left')}>
                        <ChevronLeft size={18} />
                    </button>
                    <button className={styles.navBtn} onClick={() => scroll('right')}>
                        <ChevronRight size={18} />
                    </button>
                </div>
            </div>

            <div className={styles.sliderWrapper}>
                <div className={styles.slider} ref={sliderRef}>
                    {trendingItems.map((item, index) => (
                        <motion.button
                            key={item.id}
                            className={styles.card}
                            style={{
                                background: `linear-gradient(135deg, ${item.color}dd 0%, ${item.color}99 100%)`
                            }}
                            whileHover={{ y: -4, scale: 1.02 }}
                            whileTap={{ scale: 0.98 }}
                            onClick={() => onItemClick?.(item.id)}
                            initial={{ opacity: 0, y: 20 }}
                            animate={{ opacity: 1, y: 0 }}
                            transition={{ delay: index * 0.05 }}
                        >
                            {item.isHot && (
                                <span className={styles.hotBadge}>üî• Hot</span>
                            )}
                            <span className={styles.cardTitle}>{item.title}</span>
                            <span className={styles.cardSubtitle}>{item.subtitle}</span>
                        </motion.button>
                    ))}
                </div>
            </div>
        </section>
    );
};

export default TrendingSlider;
