interface HotRightNowProps {
    onOpportunityClick?: (id: string) => void;
    onSave?: (id: string) => void;
    savedIds?: string[];
}

const HotRightNow = ({
    onOpportunityClick,
    onSave,
    savedIds = [],
}: HotRightNowProps) => {
    return (
        <section className={styles.section}>
            <div className={styles.header}>
                <div className={styles.titleWrapper}>
                    <Flame size={24} className={styles.fireIcon} />
                    <div>
                        <h2 className={styles.title}>Hot Right Now</h2>
                        <p className={styles.subtitle}>
                            Trending opportunities that are creating buzz
                        </p>
                    </div>
                </div>
                <div className={styles.actions}>
                    <Button variant="ghost" size="sm" leftIcon={<SlidersHorizontal size={16} />}>
                        Filters
                    </Button>
                    <Button variant="outline" size="sm" rightIcon={<ChevronRight size={16} />}>
                        View All
                    </Button>
                </div>
            </div>

            <motion.div
                className={styles.grid}
                initial={{ opacity: 0 }}
                whileInView={{ opacity: 1 }}
                viewport={{ once: true }}
            >
                {hotOpportunities.map((opportunity, index) => (
                    <motion.div
                        key={opportunity.id}
                        initial={{ opacity: 0, y: 20 }}
                        whileInView={{ opacity: 1, y: 0 }}
                        viewport={{ once: true }}
                        transition={{ delay: index * 0.05 }}
                    >
                        <OpportunityCard
                            opportunity={{
                                ...opportunity,
                                isSaved: savedIds.includes(opportunity.id),
                            }}
                            onClick={onOpportunityClick}
                            onSave={onSave}
                        />
                    </motion.div>
                ))}
            </motion.div>
        </section>
    );
};

export default HotRightNow;
