'use client';

import { useState, useEffect } from 'react';
import { motion } from 'framer-motion';
import Header from '@/components/layout/Header';
import HeroSection from '@/components/home/HeroSection';
import TrendingSlider from '@/components/home/TrendingSlider';
import CategoryExplorer from '@/components/home/CategoryExplorer';
import HotRightNow from '@/components/opportunities/HotRightNow';
import CTASection from '@/components/home/CTASection';
import LevelUpSection from '@/components/home/LevelUpSection';
import OnboardingModal from '@/components/onboarding/OnboardingModal';
import LevelUpModal from '@/components/game/LevelUpModal';
import AchievementToast from '@/components/game/AchievementToast';
import Confetti from '@/components/game/Confetti';
import { useGameStore } from '@/store/gameStore';
import styles from './page.module.css';

export default function Home() {
  const [isOnboardingOpen, setIsOnboardingOpen] = useState(false);
  const [mounted, setMounted] = useState(false);

  const {
    savedOpportunities,
    saveOpportunity,
    unsaveOpportunity,
    level,
    showLevelUpModal,
    closeLevelUpModal,
    showAchievementToast,
    latestAchievement,
    closeAchievementToast,
    showConfetti,
    hideConfetti,
    checkIn,
  } = useGameStore();

  useEffect(() => {
    setMounted(true);
    checkIn();
  }, [checkIn]);

  const handleGetStarted = () => {
    setIsOnboardingOpen(true);
  };

  const handleExplore = () => {
    const element = document.getElementById('trending');
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
  };

  const handleCategoryClick = (categoryId: string) => {
    console.log('Category clicked:', categoryId);
  };

  const handleOpportunityClick = (id: string) => {
    console.log('Opportunity clicked:', id);
  };

  const handleSaveOpportunity = (id: string) => {
    if (savedOpportunities.includes(id)) {
      unsaveOpportunity(id);
    } else {
      saveOpportunity(id);
    }
  };

  if (!mounted) {
    return (
      <div className={styles.loading}>
        <motion.div
          className={styles.loadingSpinner}
          animate={{ rotate: 360 }}
          transition={{ repeat: Infinity, duration: 1, ease: 'linear' }}
        />
      </div>
    );
  }

  return (
    <>
      <Header onGetStarted={handleGetStarted} />

      <main className={styles.main}>
        {/* Hero Section */}
        <HeroSection
          onGetStarted={handleGetStarted}
          onExplore={handleExplore}
        />

        {/* Trending Slider */}
        <section id="trending">
          <TrendingSlider onItemClick={handleOpportunityClick} />
        </section>

        {/* What's Your Vibe - Categories */}
        <CategoryExplorer onCategoryClick={handleCategoryClick} />

        {/* Hot Right Now - Opportunities Grid */}
        <HotRightNow
          onOpportunityClick={handleOpportunityClick}
          onSave={handleSaveOpportunity}
          savedIds={savedOpportunities}
        />

        {/* CTA Section */}
        <CTASection />

        {/* Level Up Section */}
        <LevelUpSection />

        {/* Footer */}
        <footer className={styles.footer}>
          <div className={styles.footerContent}>
            <div className={styles.footerBrand}>
              <span className={styles.footerLogo}>‚ú¶ Myark</span>
              <p>Helping students discover opportunities and build their future.</p>
            </div>
            <div className={styles.footerLinks}>
              <div className={styles.footerColumn}>
                <h4>Product</h4>
                <a href="/explore">Explore</a>
                <a href="/scholarships">Scholarships</a>
                <a href="/olympiads">Olympiads</a>
                <a href="/competitions">Competitions</a>
              </div>
              <div className={styles.footerColumn}>
                <h4>Company</h4>
                <a href="/about">About Us</a>
                <a href="/careers">Careers</a>
                <a href="/blog">Blog</a>
              </div>
              <div className={styles.footerColumn}>
                <h4>Support</h4>
                <a href="/help">Help Center</a>
                <a href="/contact">Contact</a>
                <a href="/privacy">Privacy</a>
              </div>
            </div>
          </div>
          <div className={styles.footerBottom}>
            <p>¬© 2024 Myark. All rights reserved.</p>
            <p>Made with ‚ù§Ô∏è for students in India</p>
          </div>
        </footer>
      </main>

      {/* Modals */}
      <OnboardingModal
        isOpen={isOnboardingOpen}
        onClose={() => setIsOnboardingOpen(false)}
      />

      <LevelUpModal
        show={showLevelUpModal}
        level={level}
        onClose={closeLevelUpModal}
      />

      <AchievementToast
        badge={latestAchievement}
        show={showAchievementToast}
        onClose={closeAchievementToast}
      />

      <Confetti
        show={showConfetti}
        onComplete={hideConfetti}
      />
    </>
  );
}
